import { Component, Element, Event, EventEmitter, h, Host, Prop, Watch } from '@stencil/core';
import PerfectScrollbar from 'perfect-scrollbar';
import type { NavbarItemInterface } from '../storm-navbar/interface';

@Component({
  tag: 'storm-main-content',
  styleUrl: 'storm-main-content.css',
  shadow: false,
})
export class StormMainContent {
  @Event() scroll: EventEmitter<number>;
  @Element() host: HTMLElement;

  @Prop() dataSet: NavbarItemInterface[] | string;
  @Prop() navbarPosition: string[];
  @Prop() searchable: boolean = false;

  private _dataSet: NavbarItemInterface[];

  @Watch('dataSet')
  dataSetWatcher(newValue: NavbarItemInterface[] | string) {
    if (typeof newValue === 'string') {
      this._dataSet = JSON.parse(newValue);
    } else {
      this._dataSet = newValue;
    }
  }

  componentWillLoad() {
    if (!this.dataSet) return;
    this.dataSetWatcher(this.dataSet);
  }

  componentDidLoad = () => {
    const container = this.host.querySelector('.ps');
    new PerfectScrollbar(container);
    const scroll = this.scroll;
    let lastKnownScrollPosition = 0;
    let ticking = false;

    container.addEventListener('scroll', (e: CustomEvent) => {
      // lastKnownScrollPosition = window.scrollY;
      lastKnownScrollPosition = container.scrollTop | e.detail;

      if (!ticking) {
        window.requestAnimationFrame(function () {
          scroll.emit(lastKnownScrollPosition);
          ticking = false;
        });

        ticking = true;
      }
      e.stopPropagation();
    });
  };

  render() {
    return (
      <Host class="position-absolute w-100 h-100">
        <main class="main-content position-relative max-height-vh-100 h-100 border-radius-lg ps ps--active-y">
          <storm-navbar dataSet={this._dataSet} position={this.navbarPosition} searchable={this.searchable}></storm-navbar>
          <slot name="item-container" />
        </main>
      </Host>
    );
  }
}
